.main-nav {
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  margin: 0 auto;
  padding-bottom: 238px;
  width: 880px; }
  @media (max-width: 1023px) {
    .main-nav {
      padding-bottom: 0;
      width: 100%; } }

.main-nav__bar {
  display: flex;
  justify-content: space-between;
  width: 100%; }
  @media (max-width: 1023px) {
    .main-nav__bar--open {
      min-height: 50px;
      justify-content: flex-end;
      background-color: #ffffff; } }

.main-nav__logo {
  position: absolute;
  bottom: 0;
  left: 0; }
  @media (max-width: 1023px) {
    .main-nav__logo {
      position: static;
      padding-top: 25px;
      padding-left: 21px; } }
  @media (max-width: 767px) {
    .main-nav__logo {
      padding-left: 16px; } }

@media (max-width: 1023px) {
  .main-nav__bar--open .main-nav__logo {
    display: none; } }

@media (max-width: 1023px) {
  .main-nav--js .site-list {
    display: none; } }

.main-nav__toggle {
  position: relative;
  display: none; }
  @media (max-width: 1023px) {
    .main-nav__toggle {
      display: block;
      width: 50px;
      height: 50px;
      background-color: #0ad9c6;
      border: 0; }
      .main-nav__toggle--hidden {
        display: none; }
      .main-nav__toggle:focus {
        outline: 1px solid #000000; }
      .main-nav__toggle::before, .main-nav__toggle::after {
        content: " ";
        position: absolute;
        margin-left: -11px;
        width: 22px;
        height: 1px;
        background-color: #000000; }
      .main-nav__toggle::before {
        top: 17px;
        left: 50%;
        box-shadow: 0 6px 0 #000000; }
      .main-nav__toggle::after {
        top: 30px;
        left: 50%; } }

@media (max-width: 1023px) {
  .main-nav__toggle--open {
    background-color: transparent; }
    .main-nav__toggle--open:focus {
      outline: 1px solid #0ad9c6; }
    .main-nav__toggle--open::before, .main-nav__toggle--open::after {
      content: " ";
      position: absolute;
      top: 50%;
      left: 50%;
      margin-left: 0;
      width: 32px;
      height: 1px;
      background-color: rgba(20, 21, 23, 0.2);
      box-shadow: none; }
    .main-nav__toggle--open::before {
      transform: translate(-50%, -50%) rotate(45deg); }
    .main-nav__toggle--open::after {
      transform: translate(-50%, -50%) rotate(-45deg); } }

