.site-list {
  @include list-reset;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  transition: 0.2s;
  border-bottom: 1px solid $white-20;

  @media (max-width: 1023px) {
    flex-direction: column;

    margin: 0;
    padding: 48px 0;
    width: 100%;

    background-color: $white;

    &--js {
      position: absolute;
      z-index: 1;

      top: 50px;
      left: 0;

      box-shadow: 0 5px 5px rgba(0, 0, 0, 0.25);
    }
  }
}


.site-list__link {
  position: relative;
  display: block;

  padding: 20px 24px 10px;
  margin: 0;

  font-size: 16px;
  line-height: 32px;
  font-weight: 300;
  color: $white-70;
  text-decoration: none;

  &:hover,
  &:focus {
    color: $cyan;
  }

  &:active {
    color: $grey;
  }

  @media (max-width: 1023px) {
  padding-top: 17px;
  padding-left: 16px;
  padding-right: 35px;
  margin-bottom: 17px;

  font-size: 20px;
  line-height: 28px;
  color: $grey;
  background-color: $grey-back;

  &::after {
    content: " ";
    position: absolute;

    top: 50%;
    right: 24px;

    margin-top: -6px;
    width: 8px;
    height: 12px;

    background-image: url("../img/icon-arrow-right.svg");
    background-repeat: no-repeat;
  }
}
}
